body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* css for banner */

#mybanner{
  padding:120px;
  height:400px;
  background-color:skyblue;
  font-size:80px;
  text-align:center;
  font-weight:bold;
  -webkit-animation:movebanner;
          animation:movebanner;
  -webkit-animation-duration:10s;
          animation-duration:10s;
  -webkit-animation-iteration-count:infinite;
          animation-iteration-count:infinite;

}

@-webkit-keyframes movebanner{
  20%{ background-color:slateblue; color:white; font-size:40px;}
  40%{ background-color:blue; color:white; font-size:50px;}
  60%{ background-color:brown; color:white; font-size:60px;}
  80%{ background-color:cyan; color:white; font-size:70px;}
}

@keyframes movebanner{
  20%{ background-color:slateblue; color:white; font-size:40px;}
  40%{ background-color:blue; color:white; font-size:50px;}
  60%{ background-color:brown; color:white; font-size:60px;}
  80%{ background-color:cyan; color:white; font-size:70px;}
}

